import { Session } from './../../../services/session';
import { Utility } from './../../../services/utility';
import { ConfigProvider } from './../../../providers/config-provider';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the TestBindempPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-test-bindemp',
  templateUrl: 'test-bindemp.html',
})
export class TestBindempPage {

  InCompanyDate: any;
  siteCode: string;
  result : string;
  constructor(public navCtrl: NavController, 
    
    public navParams: NavParams,
    public configProvider: ConfigProvider,
    public session: Session) {
  }

  ionViewDidLoad() {
     this.InCompanyDate = Utility.formatTime(this.session.language,new Date(),'YYYY-MM-DD') 
  }

  Test(){
    console.log(this.InCompanyDate)
    this.configProvider.AfterNewEmployeeBind(this.InCompanyDate,this.siteCode).subscribe(res=>{
      this.result = res;
    });
  }

}



